import { useReducer } from "react";
import TodoContext from "./ContextTodo";
import todoReducer from "./todoReducer";
import  axios  from "axios";
import { useCallback } from "react";
import Swal from 'sweetalert2';

const TodoProvider=({children } )=>{
     const initialState={
        todos:[],
        error:null
    }
   const[state,dispatch]=useReducer(todoReducer,initialState);
   const getData= useCallback(async()=>{
    try{
        const res=await axios.get('https://jsonplaceholder.typicode.com/todos');
        dispatch({type:'setTodo', payload:res.data})
        dispatch({type:'setError', payload:[]})
        
        

    }
    catch(error)
    {
        dispatch({type:'setError', payload:error.message});
        dispatch({type:'setTodo', payload:null})
    }
  
},[])

 const filterTodos=async(count)=>{
    try{
        const res=await axios.get(`https://jsonplaceholder.typicode.com/todos?_limit=${count}`);
        dispatch({type:'setFilter', payload:res.data})
        dispatch({type:'setError', payload:[]})
        
       

    }
    catch(error)
    {
        dispatch({type:'setError', payload:error.message});
        dispatch({type:'setFilter', payload:null})
    }
  
}


const addTodo=async(title)=>{
    try{
        const res=await axios.post("https://jsonplaceholder.typicode.com/todos",{title:title ,completed:false});
        dispatch({type:'addTodo', payload:res.data})
        dispatch({type:'setError', payload:[]})
        
        Swal.fire({
            position: 'top-end',
            toast: true,
            icon: 'success',
          
            text: 'Berjaya!',
            showConfirmButton: false,
            timer: 3000,
            timerProgressBar: true,
            backgroundColor: '#28a745',
            titleColor: '#fff',
        })

    }
    catch(error)
    {
        dispatch({type:'setError', payload:error.message});
        dispatch({type:'addTodo', payload:null})
    }
  
}



const removeTodo=async(todoId)=>{
    try{
        const res=await axios.delete(`https://jsonplaceholder.typicode.com/todos/${todoId}`);
        dispatch({type:'remove-todo', payload:todoId})
        dispatch({type:'setError', payload:[]})
        
        Swal.fire({
            position: 'top-end',
            toast: true,
            icon: 'warning',
          
            text: 'Todo removed!',
            showConfirmButton: false,
            timer: 3000,
            timerProgressBar: true,
            backgroundColor: '#28a745',
            titleColor: '#fff',
        })

    }
    catch(error)
    {
        dispatch({type:'setError', payload:error.message});
        dispatch({type:'addTodo', payload:null})
    }
  
}
const updateTodo=async(todo)=>{
    try{
        const res=await axios.put(`https://jsonplaceholder.typicode.com/todos/${todo.id}`,{title: todo.title ,completed:!todo.completed});
        dispatch({type:'update-todo', payload:res.data})
        dispatch({type:'setError', payload:[]})
        
        Swal.fire({
            position: 'top-end',
            toast: true,
            icon: 'success',
          
            text: 'Update Todo!',
            showConfirmButton: false,
            timer: 3000,
            timerProgressBar: true,
            backgroundColor: '#28a745',
            titleColor: '#fff',
        })

    }
    catch(error)
    {
        dispatch({type:'setError', payload:error.message});
        dispatch({type:'addTodo', payload:null})
    }
  
}



    return(
<TodoContext.Provider value={{...state,getData,filterTodos,addTodo,updateTodo,removeTodo}}>
{children}
</TodoContext.Provider>)
}

export default TodoProvider;