import { NavLink,Link } from "react-router-dom"
const Header = () => {
    return (<nav className="navbar navbar-expand-lg bg-body-tertiary">
        <div className="container-fluid">
            <Link className="navbar-brand" to="/">WebProg.io</Link>
            <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
          
          
            <div className="collapse navbar-collapse" id="navbarNav">
                <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                    <li className="nav-item list-style-none">
                        <NavLink className={(navData)=>navData.isActive ? "nav-link active":"nav-link"}  to="/">Home</NavLink>
                    </li>
                    <li className="nav-item list--style-none">
                        <NavLink className={(navData)=>navData.isActive ? "nav-link active": "nav-link"} to="/todos">Todos</NavLink>
                    </li>
                
                </ul>
            </div>
        </div>
    </nav>)
}
export default Header