import { type } from "@testing-library/user-event/dist/type";
import { useContext, useEffect } from "react";
import TodoContext from "../context/ContextTodo";
import FilterTodos from '../components/todos/Filter';
import CreateTodos from "../components/todos/create";
import UpdateTodo from "../components/todos/Update";
import DeleteTodo from "../components/todos/Delete";
import { useState } from "react";

const Todos=()=>{
    const  {todos,getData,error}=useContext(TodoContext);
    const[loading,setLoading]=useState(true)
    useEffect(()=>{
        const fetchData=async ()=>{
            await getData();
            setLoading(false)
        }
    // {fetch('https://jsonplaceholder.typicode.com/todos')
    // .then(res=>res.json())
    // .then(data=>todoContext.dispatch({type:'setTodo' , payload:data})) }
    // axios.get('https://jsonplaceholder.typicode.com/todos')
    // .then(res=>console.log(res.data))
    // .catch(err=>console.log(err.message))

       fetchData()
   
   
 } ,[])
    return(
        <div className="container mt-5">
            <div className="row g-3">
                <CreateTodos />
                <FilterTodos />
              { error && <div>{error}</div>}  
            {loading && <div className="col-md-12 text-center mt-5"><div className="spinner-border"></div></div> }
{todos && todos.map(todo=>(
    <div className="col-md-4">
        <div className={"card" + (todo.completed && "bg-light-subtle")}>
            <div className="card-body d-flex justify-content-between align-items-center">
    <div key={todo.id}>{ todo.completed ?<del>{todo.title}</del>:<span>{todo.title}</span>}</div>
    <div className="d-flex align-items-center">
   <UpdateTodo  todo={todo} />
   <DeleteTodo todoId={todo.id} />
   </div>
    </div> </div>
    </div>
    
    
     
))
}</div></div>
)
}

export default Todos;