import { useState,useContext } from "react";

import todoContext from "../../context/ContextTodo";
const CreateTodos=()=>{
    
    const[title,setTitle]=useState('')
    const[loading,setLoading]=useState(false)
    const {addTodo}=useContext(todoContext);
    const handleSubmit=async(e)=>{
        setLoading(true)
         e.preventDefault();
        if(title){
          await  addTodo(title);
          setLoading(false)
        }
    }
    return(
        <>
        

        <h4>Create todos</h4>
        <form className="row mt-3" onSubmit={(e)=>handleSubmit(e)}>
            <div className="col-md-6">
        <input type="text" className="form-control" onChange={(e)=>setTitle(e.target.value)} placeholder="todo title..." />
        <div className="form-text text-danger mb-3">
           {title ?'':'This is required'} 

        </div>
        
        </div>
        <div className="col-auto">
        <button type='submit btn btn-dark'> Create
        {loading && <div className="spinner-border spinner-border-sm ms-3"></div>}
        </button>
        </div>
        </form>
        </>
    )

}
export default CreateTodos